.header{
    min-height: 60px;
    display: flex;
    background-color: #f9f9f9;
    position: sticky;
    top: 0;
    z-index: 100;
}

.header__menu{
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0 20%;
    align-items: center;
}

.header .header__nav a{
    color: black;
    text-decoration: none;
    padding: 0.5rem;
    font-weight: bold;
}

.header .header__nav a:hover{
    transition: 150ms;
    border: 2px solid black;
}

.header__menu__logo img{
    width: 200px;
    object-fit: contain;
    margin: 0 20px;
}
